.services-page {
    .service-block {
        margin-top: 0;
        padding-top: 0;
        &:before {
            display: none;
        }
    }
    .card-img {
        height: 205px;
        margin-bottom: 25px;
    }
    .feedback {
        margin-top: 40px;
        &__inner {
            padding: 23px 44px;
        }
    }
}
@media(max-width: 1700px) {
    .services-page {
        .feedback {
            &__inner {
                padding: 23px 15px;
            }
        }
    }
}
@media (max-width: 1199px) {
    .services-page {
        strong {
            margin-bottom: 0;
        }
        .col-sm-3 {
            flex: 0 0 25%;
            max-width: 25%;
        }
    }
}
@media (max-width: 767px) {
    .services-page {
        .service-block .card-img {
            height: 98px;
        }
        strong {
            margin-bottom: 5px;
            font-size: 13px;
        }
        .feedback {
            margin-top: 0;
        }
    }
}
@media (max-width: 575px) {
    .services-page {
        .card-body {
            padding: 0 0 10px;
        }
        strong {
            font-size: 11px;
        }
    }
}